import React from 'react';
import axios from 'axios';
import { EmpConsumer } from '../provider/provider';


class Pagination extends React.Component {
    state = {
        reload: false,
        currentPage: null,
        recordsCount_perpage: null,

    };
    roundoffcount = 0;
    comments = [];
    commentsWithoutfilter = [];
    commentsHeader = [];
    constructor() {
        super();
        axios.get("https://jsonplaceholder.typicode.com/comments").then(data => {
            this.comments = data.data;
            this.commentsWithoutfilter = this.comments.slice();
            this.comments.pop();
            if (this.comments.length > 0) {
                this.commentsHeader = Object.keys(this.comments[0]);
                console.log(this.commentsHeader);
            }
            this.setState({
                reload: !this.state.reload,
                currentPage: 1,
                recordsCount_perpage: 10,
                roundoffcountt: ''



            })
        })
    }

    paginationCountChange = (e) => {
        this.setState({
            recordsCount_perpage: e.target.value,
            currentPage: 1
        }, this.findLastPage())

    }
    pageClick = (i, j) => {
        console.log("333333333333", i)
        if (i <= 0) {
            console.log("treuuuuuu")
            this.setState({
                currentPage: 1,

            })
        }
        else if (i >= j) {
            console.log("rrrrrrrrrrrrrrr")
            this.setState({
                currentPage: j,

            })
        }
        else {
            this.setState({
                currentPage: i,

            })
            console.log(this.state.currentPage, "cccccccccccccccc", i, j)
        }
    }

    pageDisplayRecords = () => {
        let rows = [];
        const { currentPage, recordsCount_perpage } = this.state;
        console.log(currentPage, "currentPage")
        let lastRecord = (recordsCount_perpage * currentPage) <= this.comments.length ? recordsCount_perpage * currentPage : this.comments.length
        for (let i = (currentPage - 1) * recordsCount_perpage; i < lastRecord; i++) {
            let data = this.comments;
            let TableDataRow = this.commentsHeader.map(e => <td>{data[i][e]}</td>)
            rows.push(
                <tr key={data[i].id}>
                    <td>
                        <EmpConsumer>{employee => (<button onClick={() => employee.changeEmployeeId(i + 1)}>setid={i + 1}</button>)}</EmpConsumer></td>
                    {/* <td>{data[i].name}</td>
                    <td>{data[i].id}</td>
                    <td>{data[i].postId}</td>
                    <td>{data[i].body}</td>
                    <td>{data[i].email}</td> */}
                    {TableDataRow}
                </tr>
            )

        }
        return rows;
    }
    findLastPage = () => {
        let totRecords = this.comments.length / this.state.recordsCount_perpage;
        console.log("eeeeeeee", totRecords)
        this.roundoffcount = Math.round(totRecords)
    }
    searchFilter = (e) => {
        console.log(e.target.value);
        let searchregex = new RegExp(e.target.value, 'i');

        let newComments = this.commentsWithoutfilter.filter(each => {
            return this.commentsHeader.some(heading => String(each[heading]).match(searchregex));
        })
        this.comments = newComments;
        this.setState({
            currentPage: 1,
            reload: !this.state.reload
        })
    }
    paginationHeader = () => {
        this.findLastPage();
        let paginationArray = [];
        paginationArray.push(<button onClick={() => this.pageClick(1)}  >&lt;&lt; </button>)
        paginationArray.push(<button onClick={() => this.pageClick(this.state.currentPage - 1, this.roundoffcount)} >&lt; </button>)
        let currentPage = this.state.currentPage % 5 === 0 ? this.state.currentPage - 1 : this.state.currentPage;
        let startPage = Math.floor(currentPage / 5) * 5;
        let endPage = Math.ceil(currentPage / 5) * 5;
        //blank page functionality when no records are there for the that particular page 
        if (endPage > Math.ceil(this.comments.length / this.state.recordsCount_perpage)) {
            endPage = Math.ceil(this.comments.length / this.state.recordsCount_perpage);
        }
        /**** blank page functionality ends ******/
        console.log(this.state.currentPage, startPage, endPage);
        //for loop is for displaying the buttons 
        for (let i = startPage; i < endPage; i++) {
            // If condition is for Highlightig the cuurent page//
            let pageButton = this.state.currentPage - 1 === i ?
                <button key={'button' + i} onClick={() => this.pageClick(i + 1)} className="highlight"  >{i + 1} </button> :
                <button key={'button' + i} onClick={() => this.pageClick(i + 1)}  >{i + 1} </button>
            paginationArray.push(pageButton)


            // <div>
            //     <button onClick={() => this.setState({ currentPage: this.state.currentPage - 1 })}>prev</button>
            //     <button onClick={() => this.setState({ currentPage: this.state.currentPage + 1 })}>next</button>
            // </div>
            // <button onClick={() => this.pageClick(i)}  >{i + 1} </button>



        }
        paginationArray.push(<button onClick={() => this.pageClick(this.state.currentPage + 1, this.roundoffcount)}  >&gt; </button>)
        paginationArray.push(<button onClick={() => this.pageClick(this.roundoffcount)}  >&gt;&gt; </button>)
        return paginationArray
    }



    render() {

        // let pageDisplayRecords = () => {
        //     let rows = [];
        //     const { currentPage, recordsCount_perpage } = this.state
        //     for (let i = (currentPage - 1) * recordsCount_perpage; i < recordsCount_perpage * currentPage; i++) {
        //         let data = this.comments;
        //         console.log(data[i])
        //         rows.push(
        //             <tr key={data[i].id}>
        //                 <td>{data[i].name}</td>
        //                 <td>{data[i].id}</td>
        //                 <td>{data[i].postId}</td>
        //                 <td>{data[i].body}</td>
        //                 <td>{data[i].email}</td>
        //             </tr>
        //         )
        //         console.log(data[i])
        //     }
        //     return rows;
        // }

        // let paginationHeader = () => {
        //     let totRecords = this.comments.length / this.state.recordsCount_perpage;
        //     this.roundoffcount = Math.round(totRecords)
        //     let paginationArray = []
        //     for (let i = 0; i < this.roundoffcount; i++) {

        //         let pageButton = this.state.currentPage - 1 === i ?
        //             <button onClick={() => this.pageClick(i)} className="highlight"  >{i + 1} </button> :
        //             <button onClick={() => this.pageClick(i)}  >{i + 1} </button>
        //         paginationArray.push(pageButton)
        //         // paginationArray.push(
        //         //     <button onClick={() => this.pageClick(i)}  >{i + 1} </button>



        //     }
        //     return paginationArray
        // }
        let headerRow = this.commentsHeader.map(e => <th>{e}</th>);
        headerRow.unshift(<th> action</th>)

        return (
            <div>
                {/* <button onClick={() => this.pageClick(0)}  >&lt;&lt; </button>
                <button onClick={() => this.pageClick(this.state.currentPage - 2)} >&lt; </button> */}
                {this.paginationHeader()}
                <select onChange={this.paginationCountChange} value={this.state.recordsCount_perpage}>
                    <option value="10">10</option>
                    <option value="25">25</option>
                    <option value="50">50</option>
                    <option value="75">75</option>
                </select>
                <input value={this.state.searchKey} onChange={(e) => this.searchFilter(e)} />
                <table>
                    <thead>
                        <tr>
                            {headerRow}
                        </tr>
                    </thead>
                    <tbody>

                        {this.pageDisplayRecords()}
                    </tbody>
                </table>

            </div>
        );
    }
}

export default Pagination;