    constructor() {
        super();
        Axios.get("http://localhost:3001/contractor/contractor").then(data => {
            console.log(data);
            this.contractors = data.data.data;
            this.setState({ reload: true })
        })
    }
    displayName = 'DoughnutExample';
    assignData = () => {
        let newData = this.data.datasets[0].data.slice();

        for (let i = 0; i < this.employeeArray.length; i++) {
            if (this.employeeArray[i].Age < 30) {
                newData[0] += 1
            }
            else if (this.employeeArray[i].Age > 50) {
                newData[2] += 1
            }
            else {
                newData[1] += 1
            }
        }
        
        console.log(newData);
        this.data.datasets[0].data = newData;
        console.log(this.data.datasets[0].data);
    }
    render() {
        this.assignData();
        return (
            <div>
                <h2>Contractor details based on age</h2>
                <Doughnut data={this.data} />
            </div>
        );
    }
